<template>
  <v-card>
    <v-card-title> Import </v-card-title>
    <v-card-text>
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-textarea
          outlined
          name="input-7-4"
          label="Import data"
          v-model="dataImport"
          required
          :rules="[rules.required, rules.isJsonString]"
        ></v-textarea>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn small color="darken-1" text @click="onCancel">Cancel</v-btn>
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue darken-2" v-on:click="onSave(dataImport)" :disabled="!valid">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import validation from '../../common/validation';

export default {
  props: ['closeDialogImport', 'saveImport'],
  data: () => ({
    rules: { required: validation.required, isJsonString: validation.isJsonString },
    dataImport: '',
    valid: false,
  }),
  mounted() {
    this.$refs.form.rules = [];
  },
  methods: {
    onSave(dataImport) {
      if (this.$refs.form.validate()) {
        this.saveImport(dataImport, this.onCancel);
        this.dataImport = '';
      }
    },
    onCancel() {
      this.dataImport = '';
      this.$refs.form.resetValidation();
      this.closeDialogImport();
    },
  },
};
</script>
