<template>
  <v-card :loading="isLoading" :disabled="isDisabled">
    <v-card-title
      ><span class="mr-3">{{ title }}</span>
      <app-status-chip v-if="isIntegrated" chipSize="small" state="INTEGRATED"></app-status-chip>
      <app-status-chip v-if="!isIntegrated" chipSize="small" state="UNINTEGRATED"></app-status-chip>
    </v-card-title>
    <v-card-text>
      <v-form :disabled="isDisabled" :name="formName" v-model="valid" ref="form" lazy-validation>
        <v-row>
          <v-col sm="12">
            <div v-for="field in fields" :key="field.fieldName">
              <v-text-field
                v-if="field.type == 'text' || field.type == 'number' || field.type == 'password'"
                :type="field.type"
                :label="field.label"
                v-model="field.value"
                :rules="field.rules"
              ></v-text-field>
              <v-text-field
                v-if="field.type == 'actionable-text'"
                :label="field.label"
                v-model="field.value"
                :rules="field.rules"
                :append-icon="field.fieldButton.icon"
                @click:append="field.fieldButton.click"
              >
              </v-text-field>
              <v-switch v-if="field.type == 'switch'" :label="field.label" v-model="field.value"></v-switch>
              <v-text-field
                v-if="field.type == 'password'"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="showPassword = !showPassword"
                :type="showPassword ? 'text' : 'password'"
                :label="field.label"
                v-model="field.value"
                :rules="field.rules"
              ></v-text-field>
            </div>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions class="justify-end pa-4">
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue darken-2" @click="onSave" :disabled="isDisabled"
        >Save</v-btn
      >
    </v-card-actions>
  </v-card>
</template>
<script>
import validation from '../../common/validation';
import AppStatusChip from '../Services/AppStatusChip.vue';
import * as rfLoginService from '../../services/rfLoginService';
const getFieldConfig = flowDetails => ({
  flow_ip: {
    type: 'text',
    fieldName: 'flow_ip',
    label: 'Flow IP',
    valueKey: 'flow_ip',
    value: flowDetails.flow_ip,
    rules: [validation.required],
  },
  flow_port: {
    type: 'number',
    fieldName: 'flow_port',
    label: 'Flow Port',
    valueKey: 'flow_port',
    value: flowDetails.flow_port,
    rules: [validation.required, validation.number],
  },
  flow_https: {
    type: 'switch',
    fieldName: 'flow_https',
    label: 'Enable HTTPS',
    valueKey: 'flow_https',
    value: flowDetails.flow_https,
    rules: [validation.required],
  },
  flow_username: {
    type: 'text',
    fieldName: 'flow_username',
    label: 'Flow Username',
    valueKey: 'flow_username',
    value: flowDetails.flow_username,
    rules: [validation.required],
  },
  flow_password: {
    type: 'password',
    fieldName: 'flow_password',
    label: 'Flow Password',
    valueKey: 'flow_password',
    value: null,
    rules: [validation.required],
  },
});
export default {
  name: 'flow-network-login-form',
  props: ['isIntegrated'],
  // props: ['item', 'has-custom-port-selection', 'is-disabled', 'submit', 'loading'],
  data() {
    return {
      isLoading: false,
      title: 'Flow Network Credentials',
      valid: false,
      isDisabled: false,
      isIntegrated: null,
      fieldConfigs: {},
      formName: 'flowNetworkLoginForm',
      showPassword: false,
    };
  },
  components: { AppStatusChip },
  computed: {
    fields() {
      return Object.keys(this.fieldConfigs).map(key => this.fieldConfigs[key]);
    },
  },
  mounted() {
    this.fetchFlowNetworkLoginStatus();
  },
  methods: {
    fetchFlowNetworkLoginStatus() {
      this.isLoading = true;
      rfLoginService
        .fetchDetails()
        .then(detail => {
          this.fieldConfigs = getFieldConfig(detail);
          this.isIntegrated = !!detail.flow_token;
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    onSave() {
      if (this.isDisabled) {
        return;
      }
      const valid = this.$refs.form.validate();

      if (!valid) {
        return;
      }
      const formValues = this.fields.reduce((acc, field) => {
        acc[field.valueKey] = field.value;
        return acc;
      }, {});
      this.submit(formValues);
    },
    submit(formValues) {
      this.isDisabled = true;
      formValues.flow_port = Number(formValues.flow_port);
      rfLoginService
        .update(formValues)
        .then(() => {
          this.$toasted.show('Sucessfully updated flow network credentials', { type: 'success' });
        })
        .finally(() => {
          this.isDisabled = false;
        });
    },
  },
};
</script>
