<template>
  <div>
    <v-card :loading="isfetchingData" v-if="currentView === views.CONFIGURED_LOGIN">
      <v-card-title>{{ `${config.title} Integration` }}</v-card-title>
      <v-divider v-if="isIntegrationInvalid"></v-divider>
      <v-banner single-line class="text-left" v-if="isIntegrationInvalid">
        <v-icon slot="icon" color="warning" size="36"> mdi-alert-circle-outline </v-icon>
        Integration Invalid! Please delete and re-login to use the app.
      </v-banner>
      <v-card-text class="text-left">
        <v-row>
          <v-col cols="4">
            <text-field-display label="Username" :value="loginDetails.username"></text-field-display>
          </v-col>
          <v-col cols="4">
            <text-field-display label="Password" :value="loginDetails.password"></text-field-display>
          </v-col>
          <v-col cols="12" class="text-right">
            <rbx-button
              className="mr-2"
              afterIcon="mdi-pencil"
              :click="updatePassword"
              color="secondary"
              title="Update Password"
              :is-small="true"
            ></rbx-button>
            <rbx-button color="red" afterIcon="mdi-delete" :click="onDeleteIntegration" title="Delete" :is-small="true"></rbx-button>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <system-service-login-form
      v-if="currentView === views.NOT_CONFIGURED_LOGIN"
      :fetch-config-status="fetchConfigStatus"
      :test-connection="testConnection"
      :service-name="serviceName"
      form-name="createForm"
      :title="`${config.title} Login Form`"
    ></system-service-login-form>

    <system-service-login-form
      :is-edit="true"
      :service-name="serviceName"
      :title="`${config.title} Update Form`"
      form-name="passwordUpdateForm"
      :form-detail="this.loginDetails"
      :fetch-config-status="fetchConfigStatus"
      :test-connection="testConnection"
      :on-cancel="
        () => {
          currentView = views.CONFIGURED_LOGIN;
        }
      "
      v-if="currentView === views.UPDATE_PASSWORD"
    ></system-service-login-form>

    <v-card :loading="isfetchingData" v-if="currentView === views.DELETE_INTEGRATION">
      <v-card-title class="headline"> Confirm? </v-card-title>
      <v-card-text>Do you want to delete {{ config.title }} integration?</v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="nubeGreen" text @click=" () => { currentView = views.CONFIGURED_LOGIN; } "> No </v-btn>
        <v-btn color="red darken-1" :loading="isDeletingIntegration" text @click="onDeleteIntegration"> Yes </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>
<script>
import RbxButton from '../Common/RbxButton';
import TextFieldDisplay from '../Common/TextFieldDisplay';
import SystemServiceLoginForm from './SystemServiceLoginForm';
import * as systemLoginService from '../../services/systemLoginService';
const views = {
  CONFIGURED_LOGIN: 'CONFIGURED_LOGIN',
  NOT_CONFIGURED_LOGIN: 'NOT_CONFIGURED_LOGIN',
  UPDATE_PASSWORD: 'UPDATE_PASSWORD',
  DELETE_INTEGRATION: 'DELETE_INTEGRATION',
  NONE: 'NONE',
};
const systemServiceConfig = {
  rubixService: {
    title: 'Rubix',
    name: 'rubixService',
  },
  rubixBios: {
    title: 'BIOS',
    name: 'rubixBios',
  },
};
export default {
  name: 'system-service-integration',
  props: ['serviceName', 'isDeletingSystemIntegration', 'isIntegrationInvalid'],
  components: { SystemServiceLoginForm, TextFieldDisplay, RbxButton },
  data() {
    return {
      isDeletingIntegration: false,
      isfetchingData: false,
      isConfigured: false,
      config: systemServiceConfig[this.serviceName],
      currentView: views.NOT_CONFIGURED_LOGIN,
      views: views,
      loginDetails: {},
    };
  },
  mounted() {
    this.fetchConfigStatus();
    this.testConnection();
  },
  watch: {
    isDeletingSystemIntegration: function (val, oldVal) {
      if (val != oldVal) {
        this.fetchConfigStatus();
      }
    },
  },
  methods: {
    fetchConfigStatus() {
      this.isfetchingData = true;
      systemLoginService
        .fetchConfigStatus(this.serviceName)
        .then(({ data }) => {
          this.loginDetails = data;
          this.isConfigured = true;
          this.currentView = views.CONFIGURED_LOGIN;
        })
        .catch(() => {
          this.isConfigured = false;
          this.currentView = views.NOT_CONFIGURED_LOGIN;
        })
        .finally(() => {
          this.isfetchingData = false;
        });
    },
    onDeleteIntegration() {
      this.isDeletingIntegration = true;
      systemLoginService
        .deleteSystemIntegration(this.serviceName)
        .then(() => {
          this.currentView = views.NOT_CONFIGURED_LOGIN;
          this.fetchConfigStatus();
          this.$toasted.show(`Successfully deleted ${this.config.title} Integration`);
        })
        .catch(err => {
          this.$toasted.show(`Cannot deleted ${this.config.title} Integration`, { type: 'error' });
        })
        .finally(() => {
          this.isDeletingIntegration = false;
          this.testConnection();
        });
    },
    testConnection() {
      systemLoginService
        .testSystemIntegrationConnection(this.serviceName)
        .then(data => {
          this.$store.commit('changeServicesConfigured', { [this.serviceName]: true });
        })
        .catch(err => {
          if (err && err.statusCode === '401') {
            this.$store.commit('changeServicesConfigured', { [this.serviceName]: false });
          }
        });
    },
    updatePassword() {
      this.currentView = views.UPDATE_PASSWORD;
    },
    deleteIntegration() {
      this.currentView = views.DELETE_INTEGRATION;
    },
  },
};
</script>
