<template>
  <v-card ref="configure-endpoint-view">
    <v-card-title>
      <v-btn @click="onCancel" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      <v-container fluild class="text-left">Configure Endpoints</v-container>
    </v-card-title>
    <v-card-text>
      <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '' : 'nubeGreen'}`">
        <v-tab :key="0">Rubix</v-tab>
        <v-tab :key="1">Mqtt</v-tab>
        <v-tab :key="2">Flow Network</v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab">
        <v-tab-item :key="0">
          <v-container fluild>
            <div class="flex-box justify-start mb-4">
              <rbx-button
                class="mr-2"
                :is-small="true"
                title="Import"
                color="primary"
                :click="toggleImportButton"
                after-icon="mdi-import"
                v-model="importButton"
              ></rbx-button>
              <copy-clipboard class="mr-2" :on-copy="onCopy"></copy-clipboard>
            </div>
            <v-row dense>
              <v-col sm="12" md="6">
                <system-service-integration
                  :is-deleting-system-integration="isDeletingSystemIntegration"
                  service-name="rubixBios"
                  :is-integration-invalid="isBiosConfigurationValid"
                ></system-service-integration>
              </v-col>
              <v-col sm="12" md="6">
                <system-service-integration
                  :is-deleting-system-integration="isDeletingSystemIntegration"
                  service-name="rubixService"
                  :is-integration-invalid="isRubixConfigurationValid"
                ></system-service-integration>
              </v-col>
            </v-row>
            <v-row>
              <v-col md="6" sm="12">
                <v-banner class="import-banner" elevation="3" v-model="importButton" transition="slide-y-transform">
                  <import-json-form
                    :save-import="saveImport"
                    :closeDialogImport="toggleImportButton"
                  ></import-json-form>
                </v-banner>
              </v-col>
            </v-row>
            <v-row>
              <v-col sm="12" md="6">
                <url-config-form
                  title="BIOS/Service Configuration"
                  :field-details="biosConfigFields"
                  :loading="loader.biosConfigFields"
                  :submit="values => onSubmitForm(biosConfigFields.formName, values)"
                ></url-config-form>
              </v-col>
            </v-row>
          </v-container>
        </v-tab-item>
        <v-tab-item :key="1">
          <v-container fluid>
            <v-row dense>
              <v-col sm="12" md="6">
                <url-config-form
                  title="MQTT Configuration"
                  :field-details="mqttConfigFields"
                  :loading="loader.mqttConfigFields"
                  :submit="values => onSubmitForm(mqttConfigFields.formName, values)"
                ></url-config-form>
              </v-col>
            </v-row>
          </v-container>
        </v-tab-item>
        <v-tab-item :key="2">
          <v-container fluid>
            <v-row dense>
              <v-col sm="12" md="6">
                <flow-network-login></flow-network-login>
              </v-col>
            </v-row>
          </v-container>
        </v-tab-item>
      </v-tabs-items>
    </v-card-text>
  </v-card>
</template>
<script>
import RbxButton from '../Common/RbxButton';
import CopyClipboard from './CopyClipboard';
import UrlConfigForm from './UrlConfigForm.vue';
import validation from '../../common/validation';
import FlowNetworkLogin from './FlowNetworkLogin.vue';
import ImportJsonForm from '../Common/ImportJsonForm.vue';
import { SERVICE_NAMES } from '../../constants/appConstant';
import { CHANGE_SETTINGS } from '../../constants/messageConstants';
import SystemServiceIntegration from './SystemServiceIntegration.vue';
import * as mqttConfigService from '../../services/mqttConfigService';
import * as systemLoginService from '../../services/systemLoginService';
import * as configureEndpointService from '../../services/configureEndpointService';

export default {
  name: 'configure-endpoint-view',
  props: ['on-cancel', 'initial-tab'],
  components: { RbxButton, CopyClipboard, ImportJsonForm, UrlConfigForm, SystemServiceIntegration, FlowNetworkLogin },
  data() {
    return {
      tab: this.initialTab || 0,
      isDeletingSystemIntegration: false,
      clipboardMessage: '',
      importButton: false,
      loader: {
        biosConfigFields: false,
        mqttConfigFields: false,
      },
    };
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
    mqttConfig() {
      return this.$store.state.mqttConfig;
    },
    isRubixConfigurationValid() {
      this.$store.state.isServicesConfigured;
      return !this.$store.state.isServicesConfigured.rubixService;
    },
    isBiosConfigurationValid() {
      this.$store.state.isServicesConfigured;
      return !this.$store.state.isServicesConfigured.rubixBios;
    },
    biosConfigFields() {
      return {
        formName: 'biosConfigFields',
        fieldConfigs: {
          ipAddress: {
            type: 'text',
            fieldName: 'ipAddress',
            label: 'BIOS/Service IP Address',
            valueKey: 'globalIp',
            value: this.setting.globalIp,
            rules: [validation.required, validation.isIpAddress],
          },
          portNumber1: {
            type: 'text',
            fieldName: 'portNumber1',
            label: 'BIOS Port Number',
            valueKey: 'biosServicePort',
            value: this.setting.biosServicePort,
            rules: [validation.required, validation.number],
          },
          portNumber2: {
            type: 'text',
            fieldName: 'portNumber2',
            label: 'Service Port Number',
            valueKey: 'rubixServicePort',
            value: this.setting.rubixServicePort,
            rules: [validation.required, validation.number],
          },
        },
      };
    },
    mqttConfigFields: {
      get() {
        return {
          formName: 'mqttConfigFields',
          fieldConfigs: {
            name: {
              type: 'text',
              fieldName: 'name',
              label: 'Name',
              valueKey: 'name',
              value: this.mqttConfig.name,
              rules: [validation.required],
            },
            host: {
              type: 'actionable-text',
              fieldName: 'host',
              label: 'IP Address',
              valueKey: 'host',
              value: this.mqttConfig.host,
              rules: [validation.required, validation.isIpAddress],
              fieldButton: {
                icon: 'mdi-content-copy',
                click: this.onMqttIPCopyClick,
              },
            },
            port: {
              type: 'text',
              fieldName: 'port',
              label: 'Port Number',
              valueKey: 'port',
              value: this.mqttConfig.port,
              rules: [validation.required, validation.number],
            },
            auth: {
              type: 'switch',
              fieldName: 'auth',
              label: 'Enable Auth',
              valueKey: 'auth',
              value: this.mqttConfig.auth,
              rules: [validation.required],
            },
            username: {
              type: 'text',
              fieldName: 'username',
              label: 'username',
              valueKey: 'username',
              value: this.mqttConfig.username,
              rules: [validation.required],
            },
            password: {
              type: 'password',
              fieldName: 'password',
              label: 'password',
              valueKey: 'password',
              value: this.mqttConfig.password,
              rules: [validation.required],
            },
          },
        };
      },
      set(mqttConfig) {
        return mqttConfig;
      },
    },
  },
  mounted() {
    this.getSettings();
    this.fetchMqttConfig();
  },
  methods: {
    onImport() {},
    toggleImportButton() {
      this.importButton = !this.importButton;
    },
    getSettings() {
      configureEndpointService
        .getSettings()
        .then(data => {
          this.$store.commit('changeSetting', data);
        })
        .catch(err => {});
    },
    onCopy: function (copyToClipboard) {
      const serviceSetting = {
        deviceUser: this.setting.deviceUser,
        globalIp: this.setting.globalIp,
        biosServicePort: this.setting.biosServicePort,
        rubixServiceIp: this.setting.rubixServiceIp,
        rubixServicePort: this.setting.rubixServicePort,
      };
      copyToClipboard(JSON.stringify(serviceSetting));
    },
    onSubmitForm(formName, values) {
      let updateValues = {};
      this.loader[formName] = true;
      switch (formName) {
        case this.biosConfigFields.formName:
          updateValues = { ...this.setting, ...values };
          this.updateSettings(updateValues, () => {
            this.loader[formName] = false;
          });
          break;
        case this.mqttConfigFields.formName:
          updateValues = { _id: this.mqttConfig._id, ...values };
          this.updateMqttConfig(updateValues, () => {
            this.loader[formName] = false;
          });
      }
    },
    saveImport(value, callback) {
      const data = JSON.parse(value);
      this.updateSettings(data, callback);
    },
    updateSettings(values, callback) {
      configureEndpointService
        .updateSettings(values)
        .then(res => {
          this.getSettings();
          this.deleteAllSystemIntegration();
          this.$toasted.show(CHANGE_SETTINGS.success, { type: 'success' });
        })
        .catch(err => {
          console.error(err);
          this.$toasted.show(CHANGE_SETTINGS.error, { type: 'error' });
        })
        .finally(() => {
          typeof callback === 'function' && callback();
        });
    },
    updateMqttConfig(values, callback) {
      mqttConfigService
        .update(values)
        .then(() => {
          this.$toasted.show('Successfully updated Mqtt Config');

          this.fetchMqttConfig();
        })
        .catch(err => {})
        .finally(() => {
          typeof callback === 'function' && callback();
        });
    },
    onMqttIPCopyClick() {
      if (this.biosConfigFields.fieldConfigs.ipAddress.value) {
        const host = this.biosConfigFields.fieldConfigs.ipAddress.value;
        this.mqttConfig.host = host;
      }
    },
    deleteAllSystemIntegration() {
      this.isDeletingSystemIntegration = true;
      Promise.all(
        Object.keys(SERVICE_NAMES).map(key => {
          return systemLoginService.deleteSystemIntegration(SERVICE_NAMES[key]);
        }),
      )
        .then(() => {})
        .finally(() => {
          this.isDeletingSystemIntegration = false;
        });
    },
    fetchMqttConfig() {
      mqttConfigService.fetch().then(res => {
        this.$store.commit('changeMqttConfig', res);
      });
    },
  },
};
</script>
<style>
.flex-box {
  display: flex;
  justify-content: center;
  align-items: center;
}
.import-banner .v-banner__wrapper {
  padding: 0 !important;
}
</style>
