import http from '../axios';
import BaseService from './baseService';
import endpoints from '../constants/endpoints';

class RfFlowNetworkService extends BaseService {
  constructor() {
    super(endpoints.rfFlowNetwork);
  }

  getPayloadToRequest = data => {
    return {
      ...data,
      streams: data && data.streams ? data.streams.map(item => ({ uuid: item.uuid })) : [],
    };
  };

  create = body => {
    let endpoint = this.evaluateUri();
    return http.post(endpoint, this.getPayloadToRequest(body));
  };
}

export const rfFlowNetworkService = new RfFlowNetworkService();
