import http from '../axios';
import BaseService from './baseService';
import endpoints from '../constants/endpoints';

class RfStreamService extends BaseService {
  constructor() {
    super(endpoints.rfStreams);
  }

  getPayloadToRequest = data => {
    return {
      ...data,
      flow_networks: data && data.flow_networks ? data.flow_networks.map(item => ({ uuid: item.uuid })) : [],
    };
  };

  create = body => {
    let endpoint = this.evaluateUri();
    return http.post(endpoint, this.getPayloadToRequest(body));
  };
}

export const rfStreamService = new RfStreamService();
