<template>
  <div>
    <v-card v-if="!showConfirmation">
      <v-card-title>{{ title }}</v-card-title>
      <v-card-text>
        <v-form :name="formName" v-model="valid" ref="form" lazy-validation>
          <v-text-field label="Username" v-model="username" :rules="[rules.required]"></v-text-field>
          <v-text-field
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showPassword = !showPassword"
            :type="showPassword ? 'text' : 'password'"
            :label="isEdit ? 'Current Password' : 'Password'"
            v-model="password"
            :rules="isEdit ? updatePasswordRules : [rules.required]"
          ></v-text-field>
          <v-text-field
            v-if="isEdit"
            v-model="newPassword"
            :append-icon="showNewPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showNewPassword = !showNewPassword"
            :type="showNewPassword ? 'text' : 'password'"
            label="New Password"
            required
            :rules="newPasswordRules"
          ></v-text-field>
          <v-text-field
            v-if="isEdit"
            v-model="retypePassword"
            type="password"
            label="Retype Password"
            required
            :rules="[() => retypePassword == newPassword || 'Please retype the password.']"
          ></v-text-field>
        </v-form>
        <span v-if="errorMessage" class="error-message">{{ errorMessage }}</span>
      </v-card-text>
      <v-card-actions class="justify-end pa-4">
        <v-btn x-small class="mr-1 btn-height-22 white--text" color="secondary" v-if="isEdit" @click="onCancel">Cancel</v-btn>
        <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue darken-2" @click="onSave">Save</v-btn>
      </v-card-actions>
    </v-card>
    <v-card v-if="isEdit && showConfirmation">
      <v-card-title class="headline"> Confirm? </v-card-title>
      <v-card-text>Do you want to change username/password for {{ title }} integration?</v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="nubeGreen" text @click="() => { showConfirmation = false; }"> No </v-btn>
        <v-btn color="red darken-1" :loading="isUpdatingPassword" text @click="onUpdate"> Yes </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>
<script>
import validation from '../../common/validation';
import * as systemLoginService from '../../services/systemLoginService';
export default {
  name: 'system-service-login-form',
  props: [
    'serviceName',
    'fetchConfigStatus',
    'formName',
    'title',
    'isEdit',
    'formDetail',
    'onCancel',
    'testConnection',
  ],
  data() {
    return {
      valid: false,
      errorMessage: '',
      isConfigured: false,
      showPassword: false,
      showNewPassword: false,
      showConfirmation: false,
      isUpdatingPassword: false,
      username: '',
      password: '',
      newPassword: '',
      retypePassword: '',
      rules: {
        required: validation.required,
      },
      newPasswordRules: [
        value => !!value || 'Required.',
        value => !!value.match('(?=.*?[A-Z])') || 'At least 1 Uppercase',
        value => !!value.match('(?=.*?[0-9])') || 'At least 1 Number',
        value => value.length >= 8 || 'At least 8 charaters',
      ],
      updatePasswordRules: [
        validation.required,
        // value => !!value.match('(?=.*?[A-Z])') || 'At least 1 Uppercase',
        // value => !!value.match('(?=.*?[0-9])') || 'At least 1 Number',
        // value => value.length >= 8 || 'At least 8 charaters',
      ],
    };
  },
  mounted() {
    if (this.isEdit) {
      this.username = this.formDetail.username;
    }
  },
  methods: {
    onSave() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.errorMessage = '';
      if (this.isEdit) {
        this.showConfirmation = true;
      } else {
        this.onCreate();
      }
    },
    onCreate() {
      systemLoginService
        .integrateSystemService(this.serviceName, { username: this.username, password: this.password })
        .then(data => {
          this.fetchConfigStatus();
        })
        .catch(err => {
          if (err && err.message && typeof err.message === 'string') {
            this.errorMessage = err.message;
          }
        })
        .finally(() => {
          this.testConnection();
        });
    },
    onUpdate() {
      this.isUpdatingPassword = true;
      systemLoginService
        .updateUserPassword(this.serviceName, {
          username: this.username,
          password: this.password,
          newPassword: this.newPassword,
          retypePassword: this.retypePassword,
        })
        .then(data => {
          this.fetchConfigStatus();
        })
        .catch(err => {
          if (err && err.message && typeof err.message === 'string') {
            this.errorMessage = err.message;
          }
        })
        .finally(() => {
          this.isUpdatingPassword = false;
          this.testConnection();
        });
    },
  },
};
</script>
