<template>
  <span ref="copyClipboard">
    <v-btn @click="() => onCopy(copyToClipboard)" x-small color="exportColor" class="mr-1 btn-height-22 white--text">
      Export<v-icon x-small right>mdi-export</v-icon>
    </v-btn>
  </span>
</template>
<script>
export default {
  props: ['on-copy'],
  methods: {
    copyToClipboard(message) {
      this.$copyText(message, this.$refs.copyClipboard).then(
        () => {
          this.$toasted.show('Copied to clipboard!', { type: 'success' });
        },
        () => {
          this.$toasted.show('Cannot copy to clipboard.', { type: 'error' });
        },
      );
    },
  },
};
</script>