<template>
  <v-card :loading="loading" :disabled="isDisabled">
    <v-card-title>{{ title }}</v-card-title>
    <v-card-text>
      <v-form :disabled="isDisabled" :name="formName" v-model="valid" ref="form" lazy-validation>
        <v-row>
          <v-col sm="12">
            <div v-for="field in fields" :key="field.fieldName">
              <v-text-field
                v-if="field.type == 'text' || field.type == 'password'"
                :type="field.type"
                :label="field.label"
                v-model="field.value"
                :rules="field.rules"
              ></v-text-field>
              <v-text-field
                v-if="field.type == 'actionable-text'"
                :label="field.label"
                v-model="field.value"
                :rules="field.rules"
                :append-icon="field.fieldButton.icon"
                @click:append="field.fieldButton.click"
              >
              </v-text-field>
              <v-switch
                v-if="field.type == 'switch'"
                :label="field.label"
                v-model="field.value"
              ></v-switch>
            </div>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions class="justify-end pa-4">
      <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue darken-2" @click="onSave" :disabled="isDisabled">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
export default {
  name: 'url-config-form',
  props: ['title', 'item', 'has-custom-port-selection', 'is-disabled', 'submit', 'field-details', 'loading'],
  data() {
    return {
      valid: false,
    };
  },
  computed: {
    fields() {
      return Object.keys(this.fieldDetails.fieldConfigs).map(key => this.fieldDetails.fieldConfigs[key]);
    },
    formName() {
      return this.fieldDetails.formName;
    },
  },
  methods: {
    onSave() {
      if (this.isDisabled) {
        return;
      }
      const valid = this.$refs.form.validate();

      if (!valid) {
        return;
      }
      const formValues = this.fields.reduce((acc, field) => {
        acc[field.valueKey] = field.value;
        return acc;
      }, {});
      this.submit(formValues);
    },
  },
};
</script>
