import http from '../axios';

const endpoint = '/system-service-auth';

export const integrateSystemService = (serviceName, body) => {
  return http.post(`${endpoint}/${serviceName}`, body);
};

export const updateUserPassword = (serviceName, data) => {
  return http.put(`${endpoint}/password/${serviceName}`, data);
};

export const fetchConfigStatus = serviceName => {
  return http.get(`${endpoint}/config-status/${serviceName}`, { restrictToast: true });
};

export const deleteSystemIntegration = serviceName => {
  return http.delete(`${endpoint}/${serviceName}`);
};

export const testSystemIntegrationConnection = serviceName => {
  return http.get(`${endpoint}/${serviceName}/test-connection`, { restrictToast: true });
};
